<!-- by 添少 -->
<template>
  <fixed-layout>
    <template slot="header">
      <topbar>
        <template slot="before">
          <b class="iconfont icon-back" @click="$router.go(-1)"></b>
        </template>
        发帖
        <template slot="after">
          <button class="btn" @click="handNext">下一步</button>
        </template>
      </topbar>
    </template>
    <section class="upload">
      <h3 class="fw4">上传成功</h3>
      <section class="ui-upload mc">
        <img :src="cover"/>
      </section>
      <p>注：该封面图片会出现在“约”的文章列表及文章头部</p>
    </section>
  </fixed-layout>
</template>
<script>
  import { mapState } from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'

  export default {
    name: '',
    components: {
      Topbar,
      FixedLayout
    },
    props: {},
    data () {
      return {}
    },
    watch: {},
    computed: {
      ...mapState({
        cover: state => state.write.article.cover
      })
    },
    created () {
//      setTimeout(this.init, 1000)
      this.init()
    },
    methods: {
      init () {
      },
      initData () {},
      handNext () {
        this.$router.push({
          path: 'edit'
        })
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>

  .upload {
    background-color: #fff;
    text-align: center;
    padding-bottom: 10px;
    margin-top: 10px;
    h3 {
      font-size: 24px;
      font-weight: 400;
      line-height: 65px;
      padding: 0;
      margin: 0;
    }
    p {
      font-size: 14px;
      color: #adadad;
    }
    .ui-upload {
      margin-bottom: 10px;
    }
  }

</style>



